                    label=""
                  ></el-table-column>
                </el-table>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div v-for="(item, index) in content" :key="index" class="content">
            <div class="title">
              <span style="margin-left:5px;">{{ item.contentinfotitle }}</span>
            </div>
            <div class="ql-snow">
              <div class="ql-editor" v-html="item.contentinfodetail"></div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <div>{{ tools }}</div>
        </el-tab-pane>
        <el-tab-pane label="" name="fourth">
          <div>{{ remark }}</div>
        </el-tab-pane>
        <el-tab-pane label="" name="fifth">
          <div>
            <el-table
              :data="taskrecordtableData"
              stripe
              border
              style="width:100%;"
              class="extraTable"
            >
              <el-table-column type="expand">
                <template slot-scope="props">
                  <el-form
                    label-position="left"
                    inline
                    class="demo-table-expand"
                  >
                    <el-form-item label="">
                      <span>{{ props.row.fix }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.hasException }}</span>
                    </el-form-item>
